import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './styles/App.css';
import Header from './components/Header';
import Home from './components/Home';
import Announce from './components/Announce';
import Footer from './components/Footer';
import Product from './components/Product';
import Shipment from './components/Shipment';
import Returns from './components/Returns';
import SignIn from './components/SignIn';
import Cart from './components/Cart';

function App() {
    return (
        <Router>
            <div className='app'>
                <Announce />
                <Header />
                <Switch>
                    <Route path={process.env.PUBLIC_URL + '/products'}>
                        <Product />
                    </Route>
                    <Route path={process.env.PUBLIC_URL + '/shipment'}>
                        <Shipment />
                    </Route>
                    <Route path={process.env.PUBLIC_URL + '/returns'}>
                        <Returns />
                    </Route>
                    <Route path={process.env.PUBLIC_URL + '/auth'}>
                        <SignIn />
                    </Route>
                    <Route path={process.env.PUBLIC_URL + '/cart'}>
                        <Cart />
                    </Route>
                    <Route path={process.env.PUBLIC_URL + '/'}>
                        <Home />
                        <Footer />
                    </Route>
                </Switch>
            </div>
        </Router>
    );
}

export default App;
