import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import '../styles/Header.css';
import Menu from '@material-ui/icons/MenuRounded';
import Close from '@material-ui/icons/CloseRounded';
import ShoppingCart from '@material-ui/icons/ShoppingCartRounded';
import logo from '../assets/Logo.svg';
import SideNav from './SideNav';

function Header() {
    const [active, setActive] = useState(false);
    const menu = () => {
        setActive(!active);
    };

    return (
        <div className='header'>
            <div className='header__container'>
                <button onClick={menu} className='burger__button'>
                    {active ? (
                        <Close className='header__icon-burger' />
                    ) : (
                        <Menu className='header__icon-burger' />
                    )}
                </button>
                <Link to='/' className='Link__logo'>
                    <img className='logo' src={logo} />
                </Link>
                <button className='cart__button'>
                    <Link to='/cart' className='Link__cart'>
                        <ShoppingCart className='header__icon-cart' />
                        <span>0</span>
                    </Link>
                </button>
            </div>
            <SideNav toggle={active} />
        </div>
    );
}

export default Header;
